import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import '@/assets/css/reset.css'
import '@/assets/js/index.js'
import '@/assets/css/iconfont.css'
import '@/assets/css/swiper-bundle.css'
import VueAwesomeSwiper from 'vue-awesome-swiper'
import animated from 'animate.css'
import Axios from 'axios'
import Vueaxios from 'vue-axios'

Vue.prototype.$axios = Axios;

Vue.use(VueAwesomeSwiper);
Vue.use(Vueaxios,Axios);

Vue.use(animated);

import FastClick from 'fastclick';
FastClick.attach(document.body);



import { Swipe, SwipeItem,Grid, GridItem,NoticeBar,Lazyload,CountDown,Form,Notify,SwipeCell   } from 'vant';



Vue.use(Grid).use(GridItem).use(Swipe).use(SwipeItem).use(NoticeBar).use(Lazyload).use(CountDown).use(Form).use(Notify).use(SwipeCell);

Vue.config.productionTip = false

router.beforeEach((to,from,next) =>{
  console.log(from.path)
  console.log(to.path)
  next()
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
