import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

let defaultCity = '北京';
let obj = [{name:'24'},{name:'24'},{name:'24'},{name:'24'},{name:'24'},{name:'24'},];
localStorage.arr = JSON.stringify(obj);
let username ='';
if(JSON.parse(localStorage.getItem('list'))){
  username = (JSON.parse(localStorage.getItem('list')).username);
}else{
  username = '登录/注册'
}
let arr =[];
if(JSON.parse(localStorage.getItem('list'))){
  if(JSON.parse(localStorage.getItem('Item'))){
    arr = JSON.parse(localStorage.getItem('Item'));
  }else{
    arr = [];
  }
}
let sef = JSON.parse(localStorage.arr);
export default new Vuex.Store({
  state: {
   cityname:defaultCity,
   sub:sef,
   count:0,
   username:username,
   arrc:arr
  },
  mutations: {
    changeCity(state,City){
         state.cityname = City
         localStorage.city = City
    },
    changeUsername(state,username){
       state.username = username;
    },
    setItem(state,obj){
        state.arr.push(obj);
    },
    add(state){
       state.count++;
    },
    decrease(state){
      state.count--
    }
  },
  actions: {
  },
  modules: {
  }
})
